@use 'base/variables' as v;
@use 'base/mixins' as m;

.tickets {
    padding: 5rem 0;

    h3 {
        text-align: center;
        color: v.$white;
        font-size: 3.5rem;
        margin-bottom: 1rem;
    }
}

.container-tickets {

    @include m.tablet {
        @include m.grid(2, 5rem);
    }
}

.pass {
    
    .name-pass {
        text-align: center;
        font-size: 2.8rem;
        text-transform: uppercase;
        font-weight: 700;
        color: v.$white;

    }
    &-header, 
    &-footer {
        padding: .5rem;

        p {
            text-align: center;
            color: v.$white;
            margin: 0;
        }
    }
    &-header {
        background-color: v.$pink;
        p {
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }

    &-body {
        background-color: v.$white;

        ul {
            @include m.reseat-list;

            li,
            :nth-child(odd) {
                background-color: v.$white;
                padding: 1.2rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }

    &-footer {
        background-color: v.$purple;

        p {
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
}