@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup {
    background-color: v.$pink;
    padding: 5rem 0;

    h3, p {
        text-align: center;
        color: v.$white;
    }

    .date {
        text-transform: uppercase;
        margin: 5rem;
    }
}

.container-stage {
    @include m.tablet {
        @include m.grid(2, 5rem);
    }

    p {
        color: v.$black;
    }

}

.stage {
    padding-top: 2rem;

    .name-stage {
        &:after {
            content: "";
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
        }
    }
}

.calendar {
    @include m.reseat-list;
    
    li {
        padding: 2.5rem;
        font-weight: 700;

        span {
            font-weight: 400;
        }
    }
}

.edm .name-stage:after {
    background-image: url(../img/icono_edm.png);
}
.rock .name-stage:after {
    background-image: url(../img/icono_rock.png);
}

.bg-yellow {
    background-color: v.$yellow;

    li:nth-child(odd) {
        background-color: darken(v.$yellow, 20%);
    }
}

.bg-green {
    background-color: v.$green;

    li:nth-child(odd) {
        background-color: darken(v.$green, 10%);
    }
}